*{
    background-color: BLACK;
}

#title {
    justify-content: center;
    align-items: center;
    position: relative;
    width: 1000px;
    height: 75px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 10px;
    border: 5px solid rgb(240, 156, 156);
    border-radius: 15px;
    text-align: center;
    font-size: 65px;
    font-family: 'Titillium Web', sans-serif;
    -webkit-text-fill-color: transparent;
    background-image: -webkit-linear-gradient(#f1b6d3 15%, #ffffff 65%, #02b2e7 80%);
    -webkit-background-clip: text;
}

.gamebox {
    margin-top: 10px;
    display: flex;
    flex-direction: column;
}

#game-canvas {
    margin-left: auto;
    margin-right: auto;
    margin-top: 10px;
    position: relative;
    border: 5px solid rgb(240, 156, 156);
    border-radius: 20px;
    background-image: -webkit-linear-gradient(#39245f 15%, #b92e2e 65%, #3e6f7e 80%);
}

#social {
    position: relative;
    // border: 1px solid white;
    list-style: none;
    display: flex;
    width: 15%;
    // left: 40%;
    justify-content: space-between;
    font-size: 50px;
    margin: auto;
    margin-top: 20px;
}


#intro{ 
    left: 30%;
    top: 20%;
    position: fixed;
    padding: 10px;
    width: 450px;
    height: 300px;
    border: 3px solid white;
    color:rgb(221, 131, 131);
    border-radius: 15px;
    z-index: 100;
    visibility: visible;
    // margin: 10px 0 20px 0;
    margin: auto;
    border-top: 1px solid #999;
    background-image: linear-gradient(to top, #172020 0%, #330867 100%);
}

#intro > p {
    padding: 10px;
    font-size: 20px;
    text-align: justify;
    background: transparent;
}

#start-btn {
    width: 100px;
    height: 25px;
    position: relative;
    left: 35%;
    top: -5%;
    color: white;
    background-color: cadetblue;
    border: none;
    
}




.stats {
    width: 200px;
    color: white;
    background-color: transparent;
    position: fixed;
    z-index: 100;
    list-style: none;
    left: 40%;
    justify-content: left;
    top: 90px;
    font: 30px;
}

#level, #gold {
    width: 125px;
    background: linear-gradient(to right, hsl(200 100% 62%), hsl(1000 100% 59%));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    text-align: left;
}



#game-over {
    top: 25%;
    left: 40%;
    position: fixed;
    width: 200px;
    height: 300px;
    border: 3px solid white;
    border-radius: 15px;
    // margin-left: auto;
    // margin-right: auto;
    color: white;
    visibility: hidden;
    text-align: center;
}

#reset-btn {
    width: 100px;
    height: 25px;
    color: white;
    background-color: cadetblue;
    top: -10px
}

#shark-smile {
    position: relative;
    width: 150px;
    top: 10%;
}


